
<style lang="less">
  .headers {
    display: flex;
    height: 2.5rem;
    background-color: #fff;
    position: relative;
    div {
      flex: 2;
      padding-left: 1.25rem;
      height: 2.5rem;
      vertical-align: middle;
      img {
        width: 1.25rem;
        height: 1.875rem;
        vertical-align: middle;
      }
    }
    span {
      vertical-align: middle;
      line-height: 1.875rem;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  } // 头部
  nav {
    width: 100%;
  }
  nav>img {
    width: 100%;
    height: 100%;
  }
  /* 设置头部图片 END */
  /* 设置中部课程名称/报名人数/讲师 头像/讲师名称 START */
  .middle_course_title {
    width: 100%;
    display: -webkit-flex;
    display: flex;
  }
  .middle_course_title>div:nth-child(1) {
    -webkit-box-flex: 7;
    flex: 7;
  }
  .middle_course_title>div:nth-child(2) {
    -webkit-box-flex: 3;
    flex: 3;
  }
  .middle_course_title>div:nth-child(2)>img {
    width: 40%;
    float: left;
    border-radius: .625rem;
    border: 20px solid #aaa;
  }
  .middle_course_title>div:nth-child(2)>a {
    display: inline-block;
    color: #777;
  }
  /* 设置中部课程名称/报名人数/讲师 头像/讲师名称 END */
  /* 底部内容-->课程简介 START*/
  article {
    padding: 15px 0 0 0;
    box-sizing: border-box;
    width: 100%;
    &>p:nth-child(2) {
      width: 100%;
      &>img {
        width: 100%;
        height: 100%;
      }
    }
    img {
      width: 100%;
    }
  }
  /* 底部内容-->课程简介 END */
  /* 设置底部我要报名 START */
  footer {
    width: 100%;
    text-align: center;
  }
  footer>a>img {
    width: 50%;
  }
  /* 设置底部我要报名 END */
</style>

<template>
  <div v-if="carouselFigure">
    <!-- 设置返回按钮 START-->
    <headerNavBack :centerTitle="centerTitle" :rightTitle="rightTitle"></headerNavBack>
    <section class="middle_course_title">
    </section>
    <article v-html="page && page.content ? page.content : ''">{{page && page.content ? page.content : ''}}</article>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  export default {
    components: {
      headerNavBack
    },
    data() {
      return {
        // banner详细信息
        carouselFigure: Object,
        // 简介
        page: Object,
        // 以下是标题设置
        centerTitle: '',
        rightTitle: ''
      }
    },
    created () {
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.getPageInfo()
    },
    methods: {
      // 返回上一步
      back() {
        this.$router.back(-1)
      },
      // 获取课程信息
      getPageInfo(){
        this.axios.post('/api/courses/getPage', {
          carouselFigureId: this.$route.params.id
        }).then(data => {
          this.centerTitle = data.data.result.page.title
          this.carouselFigure = data.data.result.carouselFigureE
          this.page = data.data.result.page
          this.$Indicator.close()
        }).catch(err => {
          console.log(err)
        });
      },
    }
  }
</script>



